:root {
  --background: #0a0a0a;
  --foreground: #f5f5f5;
  --gold: #c9a962;
  --gold-light: #e5d4a1;
  --gray-dark: #1a1a1a;
  --gray-medium: #2a2a2a;
  --gray-light: #888888;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans), serif;
}
html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--gray-dark);
}
::-webkit-scrollbar-thumb {
  background: var(--gold);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--gold-light);
}
::selection {
  background: var(--gold);
  color: var(--background);
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
.animate-fade-in {
  animation: fadeIn 0.8s ease-out forwards;
}
.animate-slide-in {
  animation: slideIn 0.6s ease-out forwards;
}
.hover-gold {
  transition: color 0.3s ease;
}
.hover-gold:hover {
  color: var(--gold);
}
.img-zoom {
  transition: transform 0.5s ease;
}
.img-zoom:hover {
  transform: scale(1.05);
}
.btn-primary {
  background: transparent;
  border: 1px solid var(--gold);
  color: var(--gold);
  padding: 12px 32px;
  font-size: 14px;
  letter-spacing: 2px;
  text-transform: uppercase;
  transition: all 0.3s ease;
  cursor: pointer;
}
.btn-primary:hover {
  background: var(--gold);
  color: var(--background);
}
.btn-secondary {
  background: var(--gold);
  border: 1px solid var(--gold);
  color: var(--background);
  padding: 12px 32px;
  font-size: 14px;
  letter-spacing: 2px;
